import React, { useState } from "react";

import Logo from "./Logo";
import { createBrowserHistory } from "history";
import { useLocation, useSearchParams, useNavigate,useHistory } from "react-router-dom";
import { getLang, __ } from "../../helper/utils";

const Header = () => {
	const navigate = useNavigate();
	const [searchParams] = useSearchParams();
	const location = useLocation();
	const urlParams = new URLSearchParams(location.search);
	let tpk_id;
	tpk_id = urlParams.get("tpk_id");
	if (!tpk_id) tpk_id = searchParams.get("tpk_id");
	let image = urlParams.get("image");
	if (!image) image = searchParams.get("image");

	let lang = window.__current_language;

	const toggleMenu = (e) => {
		const site = document.querySelector(".site");
		if (site.classList.contains("site--shown-navigation")) {
			site.classList.remove("site--shown-navigation");
		} else {
			site.classList.add("site--shown-navigation");
		}
	};

	const changeLang = (event) => {
		let lang = event.target.getAttribute("data-lang");
		window.__current_language = lang;

		event.preventDefault();
		if (location.pathname === "/") {
			navigate(`/?tpk_id=${tpk_id}&image=${encodeURIComponent(image)}&lang=${lang}`)
		} else {
			navigate(`${location.pathname}?lang=${lang}`);
		}
	};

	return (
		<section className="site__navigation site__navigation--white">
			<div className="container flex flex--end">
				<div className="brand">
					<a href="https://unikbase.com" target="_blank" rel="noreferrer">
						<Logo className="site__logo" />
					</a>
				</div>
				<div className="hamburger d-mobile" onClick={toggleMenu}>
					<div></div>
				</div>

				<div className="menu--mobile-float">
					<div className="navigation main-navigation menutext flex">
						<ul className="menu menu--horizontal">
							<li className="">
								<a
									target="_blank"
									rel="noreferrer"
									href="https://unikbase.com/#what-we-do"
									title={__("What we do")}
								>
									{__("What we do")}
								</a>
							</li>
							<li className="">
								<a
									target="_blank"
									rel="noreferrer"
									href="https://unikbase.com/#how-it-works"
									title={__("How it works")}
								>
									{__("How it works")}
								</a>
							</li>
							<li className="">
								<a
									target="_blank"
									rel="noreferrer"
									href="https://unikbase.com/#about-us"
									title={__("About us")}
								>
									{__("About us")}
								</a>
							</li>
							<li className="">
								<a
									target="_blank"
									rel="noreferrer"
									href="https://unikbase.com/#footer"
									title={__("Contact")}
								>
									{__("Contact")}
								</a>
							</li>
						</ul>
					</div>

					<div className="navigation flex margin--l">
						<div className="dropdown-menu sign-in">
							<span className="handle">{__("Sign in")}</span>
							<ul className="menu menu--vertical text--align-right">
								<li className="">
									<a
										href="https://unikbase.com"
										target="_blank"
										rel="noreferrer"
										title={__("Operator Sign in")}
									>
										{__("Operator Sign in")}
									</a>
								</li>
								<li className="">
									<a
										href="https://unikbase.com"
										target="_blank"
										rel="noreferrer"
										title={__("Collector Sign in")}
									>
										{__("Collector Sign In")}
									</a>
								</li>
							</ul>
						</div>

						<div className="dropdown-menu languages">
							<span data-active-lang="en" className="handle">
								EN
							</span>
							<ul className="menu menu--languages">
								<li className={lang !== "fr" ? "active" : ""}>
									<a data-lang="en" href="#" onClick={changeLang} alt="">
										<i className="icon icon--global"></i>EN
									</a>
								</li>
								<li className={lang === "fr" ? "active" : ""}>
									<a data-lang="fr" href="#" onClick={changeLang} alt="">
										<i className="icon icon--global"></i>FR
									</a>
								</li>
							</ul>
						</div>
					</div>
				</div>
			</div>
		</section>
	);
};

export default Header;
